@import '../../common/styles/mixin';
@import '../../common//styles/variables';

.about-us-container {
  .banner_section {
    .about-banner {
      width: 100%;
      height: 100vh;
      background: url('../../assets/images/others/hero-elephant.jpg') center center/cover no-repeat fixed rgba(66,66,41,0.6);
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;

      @include font('Roboto', 50px, 600, $white);
    }
  }

  .about-us-body {
    .about-us-form {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;

      h2 {
        padding: 40px 0;
        @include font('Roboto', 40px, 600);
      }

      p {
        padding: 0px 0 20px;
        line-height: 25px;
        @include font('Roboto', 18px, 400, $description);
      }

    }
  }

  .MuiTextField-root {
    width: 100%;
  }

}

