@import '../../common/styles/mixin';
@import '../../common//styles/variables';

.home-container {
  .banner_section {
    position: relative;
    z-index: 1;
    // .home-banner {
    //     width: 100%;
    //     height: 100vh;
    //     background: url('../../assets/images/home_banner.jpg') no-repeat;
    //     background-color: rgba(66,66,41,0.6);
    //     background-size: cover;
    //     background-position: center center;
    //     background-attachment: fixed;
    //     display: flex;
    //     justify-content: center;
    //     flex-direction: column;
    //     align-items: center;

    //     @include font('Roboto', 50px, 600, $white);
    // }

    .carousel-item {
      img {
        width: 100%;
        height: 100vh;

        @media(max-width: 700px) {
          height: 400px;
        }
      }

      .banner-text {
        position: absolute;
        top: 50%;
        right: 50%;
        -webkit-transform: translate(50%, -50%);
        transform: translate(50%, -50%);
        z-index: 10;
        color: #fff;
        text-align: center;

        h2 {
          padding-bottom: 20px;
          text-transform: uppercase;
        }

        button {
          padding: 9px 50px;
        }
      }
    }
  }
}

.testimonial_container {
  @extend .how-does-it-work_container;   
  .work-flow_blocks{
    .MuiCardContent-root {
      min-height: 220px;
      max-height: 220px;
    }
    svg {
      font-size: 40px;
    }
  }

}

.how-does-it-work_container {
  padding: 60px 0;
  background: $lightBlue;
  .wrapper {
    h2 {
      padding: 30px 0 40px;
      text-align: center;
      @include font('Roboto', 35px, 600);
    }
  }

  .MuiPaper-root {
    margin-bottom: 25px;

    .MuiAvatar-colorDefault {
       color: $soilColor;
    }

    .MuiTypography-root {
      text-align: left;
    }
  }

  .MuiCardContent-root {
    min-height: 280px;
  }
}

.help-me_container {
  background: url('../../assets/images/help-me.jpg') right/cover no-repeat fixed rgba(66,66,41,0.6);

  .help-me_info {
    height: 556px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: flex-start;

    h2 {
      margin-bottom: 40px;
      @include font('Roboto', 35px, 600, $white);
    }

    h3 {
      margin-bottom: 30px;
      @include font('Roboto', 28px, 600, $white);
    }

    p {
      margin-bottom: 30px;
      @include font('Roboto', 20px, 300, $white);

      strong {
        font-weight: 600;
      }
    }
    button {
      width: 200px;
    }
  }
}

.why-us_container {
  @extend .how-does-it-work_container;   
  .MuiCardContent-root {
    min-height: 190px;
    max-height: 190px;
  }
}

.packages_container {
  background: $lightBlue;
  @extend .how-does-it-work_container;

  .work-flow_blocks {
    .info-card_container {
      cursor: pointer;
      transition: all .3s;

      &:hover {
        box-shadow: 0 0 10px 0 $lightSoilColor;
        transform: scale(1.05);
      }

      .MuiCardHeader-root {
        padding: 16px 0;
      }

      .duration {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        @include font('Roboto', 14px, 600, $soilColor);

        span {
          padding-left: 10px;
        }
      }
    }
  }
}

.who-are-we_container {
  padding: 75px 0;

  h2 {
    margin-bottom: 24px;
    @include font('Roboto', 35px, 600);
  }

  p {
    color: $description;

    font-size: 16px;
    line-height: 1.5;
  }
}
